var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('article',{staticClass:"baking"},[_c('section',{staticClass:"baking__ch baking__ch--1"},[_c('h1',{staticClass:"baking__ch-title"},[_vm._v("餅舖式微 返鄉思考")]),_c('section',{staticClass:"baking__ch-content"},[_c('p',[_vm._v("\n        「祥美餅舖」位於水林市場附近，以預約訂購方式營運的傳統糕餅店。李重賢老闆曾經在台中上班，目前是餅舖的第二代。從小就他在家裡幫忙，每逢過年過節店裡總是應接不暇。他說：「當初會回來接手是因為父親沒有考慮外傳，並且打算歇業，傳統餅鋪對於地方有一定需求，後來就與老婆商量，2000年決定回來繼承父業。」\n      ")]),_c('img',{staticClass:"--size-m --center",attrs:{"src":(_vm.publicPath + "img/detail/baking/mold.jpg"),"alt":"mold"}}),_c('p',[_vm._v("\n        統糕點隨著時代和消費市場的改變，加上市場多元化競爭，大多數的店家逐漸式微。「剛回來接手經常和父親意見不合，他有他的做法和固定客源。我需要協助他之外，另一方面為了因應消費市場，我必需想辦法找出新客源，做出成果得到父親和消費者的肯定。」李老闆說。\n      ")])])]),_c('section',{staticClass:"baking__ch baking__ch--2"},[_c('h1',{staticClass:"baking__ch-title"},[_vm._v("傳承繁複作法 開拓忠實紛絲")]),_c('section',{staticClass:"baking__ch-content"},[_c('p',[_vm._v("\n        李老闆憑著過去業務的閱歷，深信產品需要在不同的市場作出區隔。他說：「我母親不喜歡加工類的罐頭，過年香腸自己灌、粿自己蒸，至今80歲從不喜歡外食，總喜歡自己煮東西，自己熬高湯、炸豬油和紅蔥，就是為了一碗麵，不管再怎麼繁雜的手續，對她來說“自己煮的最安心”。」從小李老闆便在這樣的環境下，儘管製作油飯、肉餅等的程序再怎麼繁複，依然謹遵父親遺言：「先求良心，再求美味」。並努力做出自己家傳統細膩的口味與質感，希望呈現出手工又有溫度，口味道地的食品，來因應新客源。\n      ")]),_c('img',{staticClass:"--size-m --center",attrs:{"src":(_vm.publicPath + "img/detail/baking/two_ricecake.jpg"),"alt":"two_ricecake"}})])]),_c('section',{staticClass:"baking__ch baking__ch--3"},[_c('h1',{staticClass:"baking__ch-title"},[_vm._v("傳統餅舖 陪伴我們平安成長")]),_c('section',{staticClass:"baking__ch-content"},[_c('p',[_vm._v("\n        李老闆說：「早期地方的餅舖，因為農村在衛生醫療有限的時代裡，小孩很容易夭折。每家如果新生兒誕生，那就一件令人開心又可以分享的「喜事」，期待小孩能平安順利長大。」\n      ")]),_c('p',[_vm._v("\n        第一個階段小孩出生滿一個月稱為「滿月」，滿月需要準備油飯與紅圓，紅圓代表奶水，象徵圓潤豐滿的乳房，表達「圓滿」之意。第二個階段是滿四個月稱為「四月日(收涎)」，四月日的糕點是「四月桃」，表示小孩可以出去「𨑨迌」(台語遊玩)。第三階段是感謝老天賜福小孩長大，這時要準備「紅龜粿」，期盼小孩能如龜長壽。這三個階段的糕點數量要以數字12來代表一年(當年閏月就做13個)。最後一個階段是16歲成年禮的「脫絭」。如果小孩請神明收為契約子女者，要準備「福祿壽」的供品組合，表示一個完整的祝福，一個生命成長的確立。\n      ")]),_c('p',[_vm._v("\n        經過他的解釋後，我們深深的了解地方餅舖其實是最貼近鄰里，替民眾舉辦慶典的原型，更不可或缺的存在。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/baking/making_ricecake.jpg"),"alt":"making_ricecake"}})])]),_c('section',{staticClass:"baking__ch baking__ch--4"},[_c('h1',{staticClass:"baking__ch-title"},[_vm._v("嚴選原料 和在地農友合作")]),_c('section',{staticClass:"baking__ch-content"},[_c('p',[_vm._v("\n        老闆娘王雅鈴說：「我們的紅豆餡、綠豆餡、鳳梨餡大都是我先生自己炒、煮、濾以及熬糖、收汁，幾乎是12小時慢慢熬煮不停火。」「豆餡需要掌握好乾濕度和軟硬度。“保鮮越短、生命越長”是祥美的宗旨，尤其紅龜粿因沒放防腐劑，夏天容易腐壞，所以要凌晨早起製作，當天現做現賣，沒吃完就要冷凍保鮮。」李老闆又說。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/baking/紅龜粿過程.jpg"),"alt":"紅龜粿過程"}}),_c('p',[_vm._v("\n        餅舖的紅豆、花生和紅蔥頭、紫米、香菇等原料都是和在地農友以及小農配合且精心挑選的。因為原料是傳遞食物本身的香氣、餘韻和口感，影響食品質地的重要關鍵。\n      ")])])]),_c('section',{staticClass:"baking__ch baking__ch--5"},[_c('h1',{staticClass:"baking__ch-title"},[_vm._v("濃濃古早味 深深故人情")]),_c('section',{staticClass:"baking__ch-content"},[_c('p',[_vm._v("\n        有時過年前，地方農友會也拿著自己收成的花生，請餅舖協助代工製作花生糖，在年節時送給親朋好友。除花生糖外也曾幫忙代工紅龜粿，早期喜事會請全村莊動員製作，隨著人丁減少，餅舖就承接這樣的服務。壽桃、大餅、發粿、粳粽、油飯、素三牲等，皆是一年四季需要的供品。\n      ")]),_c('p',[_vm._v("\n        餅鋪的人手雖然並不多，但他們是定期支持「華山基金會」做社會關懷，提供禮盒給獨居老人的小企業。「我們秉持“做對又要做好”的理念，來維持這家60年的店舖。」王老闆娘說。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/baking/炒花生2.jpg"),"alt":"炒花生"}})])])])}
